.App {
  font-family: sans-serif;
  text-align: center;
}

.shop-item-card-container,
.cart-item-card-container {
  display: flex;
  flex-wrap: wrap;
}

.item-card {
  width: 240px;
  margin: 20px;
}

.sort-dropdown-menu {
  float: left;
  margin-left: 40px;
}

.shop-cart-icon {
  font-size: 36px;
}

.shop-container {
  padding: 20px;
}

.shop-menu-container {
  position: relative;
  text-align: right;
  right: 40px;
}

.cart-container {
  padding: 20px;
}

.cart-summary-container {
  position: relative;
  text-align: right;
  right: 60px;
}

.no-stock-warnning {
  color: red;
  font-weight: 800;
}

.cart-summary-item-row {
  width: 300px;
  text-align: left;
  margin-left: auto;
}

.cart-summary-item-value {
  float: right;
}
